import React, { useCallback, useRef } from 'react'
import { Link, useHistory } from 'react-router-dom'
import {
  FiArrowLeft,
  FiUser,
  FiCalendar,
  FiPhone,
  FiMail,
  FiLock,
} from 'react-icons/fi'
import { Form } from '@unform/web'
import { FormHandles } from '@unform/core'
import * as Yup from 'yup'

import api from '../../services/api'

import { useToast } from '../../hooks/toast'
import getValidationErrors from '../../utils/getValidationErrors'

import doctorsImg from '../../assets/images/doctors.svg'

import Input from '../../components/Input'
import Select from '../../components/Select'
import Button from '../../components/Button'

import { Container, Content, AnimationContainer, Background } from './styles'

interface ISignUpFormData {
  name: string
  birthday: string
  sex: string
  cellphone: string
  email: string
  password: string
  passwordConfirmation: string
  isAdmin: boolean
}

interface IHandleSubmitCallback {
  (data: ISignUpFormData): void
}

const SignUp: React.FC = () => {
  const formRef = useRef<FormHandles>(null)

  const { addToast } = useToast()
  const history = useHistory()

  const handleSubmit = useCallback<IHandleSubmitCallback>(
    async data => {
      formRef.current?.setErrors({})

      try {
        const schema = Yup.object().shape({
          name: Yup.string().required('Nome obrigatrio'),

          birthday: Yup.string().min(8, 'Data de Nascimento obrigatria'),

          sex: Yup.string()
            .required('Sexo obrigatrio')
            .oneOf(['M', 'F'], 'Sexo Invlido'),

          cellphone: Yup.string().required('Telefone obrigatrio'),

          email: Yup.string()
            .required('E-mail obrigatrio')
            .email('Digite um E-mail vlido'),

          password: Yup.string().required('Senha obrigatria'),

          passwordConfirmation: Yup.string()
            .oneOf([Yup.ref('password'), undefined], 'Senhas no coincidem')
            .required('Confirmao de Senha Obrigatria'),
        })

        await schema.validate(data, {
          abortEarly: false,
        })

        data.cellphone = data.cellphone.replace(/\D+/g, '')
        delete data.passwordConfirmation
        data.isAdmin= false

        await api.post('user', {
          ...data,
        })

        addToast({
          type: 'success',
          title: 'Cadastro realizado!',
          description: 'Voc j pode fazer o seu logon no Sallut!',
        })

        history.push('/')
      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidationErrors(err)

          formRef.current?.setErrors(errors)
          return
        }

        console.log(err.response)

        addToast({
          type: 'error',
          title: 'Erro no cadastro',
          description: 'Ocorreu um erro ao fazer cadastro, tente novamente.',
        })
      }
    },
    [addToast, history],
  )

  return (
    <Container>
      <Background>
        <img src={doctorsImg} alt="medicine" />
      </Background>

      <Content>
        <AnimationContainer>
          <Form ref={formRef} onSubmit={handleSubmit}>
            <h1>Bem-vindo!</h1>
            <h3>Faa o seu cadastro</h3>

            <Input
              name="name"
              icon={FiUser}
              placeholder="Nome Completo"
              type="text"
            />

            <Input
              name="birthday"
              icon={FiCalendar}
              placeholder="Data de Nascimento"
              type="date"
            />

            <Select
              name="sex"
              icon={FiUser}
              placeholder="Sexo"
              options={[
                { label: 'Masculino', value: 'M' },
                { label: 'Feminino', value: 'F' },
              ]}
            />

            <Input
              name="cellphone"
              icon={FiPhone}
              placeholder="Celular"
              type="tel"
            />

            <Input
              name="email"
              icon={FiMail}
              placeholder="E-mail"
              type="email"
            />

            <Input
              name="password"
              icon={FiLock}
              placeholder="Senha"
              type="password"
            />

            <Input
              name="passwordConfirmation"
              icon={FiLock}
              placeholder="Confirmao da Senha"
              type="password"
            />

            <Button type="submit">Cadastrar</Button>
          </Form>

          <Link to="/">
            <FiArrowLeft />
            J possui conta? Faa Login!
          </Link>
        </AnimationContainer>
      </Content>
    </Container>
  )
}

export default SignUp