import React, { useCallback, useRef, useState } from 'react'
import { Link } from 'react-router-dom'
import { FiLogIn, FiMail } from 'react-icons/fi'
import { Form } from '@unform/web'
import { FormHandles } from '@unform/core'
import * as Yup from 'yup'

import { useToast } from '../../hooks/toast'
import getValidationErrors from '../../utils/getValidationErrors'
import api from '../../services/api'

import medicineImg from '../../assets/images/medicine.svg'

import Input from '../../components/Input'
import Button from '../../components/Button'

import { Container, Content, AnimationContainer, Background } from './styles'

interface IForgotPasswordFormData {
  email: string
}

const ForgotPassword: React.FC = () => {
  const [loading, setLoading] = useState(false)
  const formRef = useRef<FormHandles>(null)

  const { addToast } = useToast()
  // const history = useHistory()

  const handleSubmit = useCallback(
    async (data: IForgotPasswordFormData) => {
      setLoading(true)
      formRef.current?.setErrors({})

      try {
        const schema = Yup.object().shape({
          email: Yup.string()
            .required('E-mail obrigatrio')
            .email('Digite um e-mail vlido'),
        })

        await schema.validate(data, {
          abortEarly: false,
        })

        // Reclaim Password

        await api.post('/password/forgot', {
          email: data.email,
        })

        addToast({
          type: 'success',
          title: 'E-mail de recuperao enviado',
          description:
            'Enviamos um e-mail para confirmar a recuperao de senha, cheque sua caixa de entrada',
        })
      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidationErrors(err)

          formRef.current?.setErrors(errors)

          return
        }

        addToast({
          type: 'error',
          title: 'Erro na recuperao de senha',
          description:
            'Ocorreu um erro ao tentar realizar a recuperao de senha, tente novamente.',
        })
      } finally {
        setLoading(false)
      }
    },
    [addToast],
  )

  return (
    <Container>
      <Content>
        <AnimationContainer>
          <Form ref={formRef} onSubmit={handleSubmit}>
            <h1>Recuperar Senha</h1>

            <Input
              name="email"
              icon={FiMail}
              placeholder="E-mail"
              type="email"
            />

            <Button loading={loading} type="submit">
              Recuperar
            </Button>
          </Form>

          <Link to="/">
            <FiLogIn />
            Voltar ao login
          </Link>
        </AnimationContainer>
      </Content>

      <Background>
        <img src={medicineImg} alt="doctors" />
      </Background>
    </Container>
  )
}

export default ForgotPassword
